/** 页面颜色 **/
/** 页面的固定宽度 **/
.m-content {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 8.7rem;
}
.m-content .content-wrap {
  display: none;
  min-height: 100%;
  background: #F6F6F6;
  padding: 1.0rem 1.6rem;
}
.m-content .content-wrap.selected {
  display: block;
}



