/** 页面颜色 **/
/** 页面的固定宽度 **/
.m-albums {
  margin: 0rem auto;
  width: 120rem;
  background: #FFF;
  min-height: 50.0rem;
  border-radius: 0.8rem;
  border: 0.1rem solid #e9e9e9;
}
.m-albums .toolbar {
  padding: 1.0rem;
  border-bottom: 0.1rem solid #e9e9e9;
}
.m-albums .toolbar .ant-btn:first-child {
  margin-right: 1.0rem;
}



